import React from "react";
import { Route, Switch } from "react-router-dom";
import ProductosState from "../../context/admin/productosState";
import CarritoState from "../../context/carrito/carritoState";
import CarritoHeader from "./screens/carritos/CarritoHeader";
import CarritoHome from "./screens/carritos/CarritoHome";
import CarritoProductos from "./screens/carritos/CarritoProductos";


const CarritoRouter = () => {
  return (
    <>
    <CarritoHeader/>
       <CarritoState> 
       <ProductosState>
      <Switch>
      <Route path="/home" component={CarritoHome} />
      <Route path="/cart" component={CarritoProductos} />
      </Switch>
      </ProductosState>
       </CarritoState> 
    </>
  );
};

export default CarritoRouter;
