import React, { useContext } from 'react'
import ProductosContext from '../../../../context/admin/productosContext';
import CarritoFiltro from './CarritoFiltro';


const CarritoProductos = () => {

    const {productos,termino,setTermino}=useContext(ProductosContext);


    return (
        <>
        <CarritoFiltro setTermino={setTermino}/>
      <main className ="container-fluid mt-5" >
        
          {
                termino.trim().length >2 ?
                <div className="row">
                <div className="col 12">
                    <h3>Resultados para la búsqueda :{termino}</h3>
                </div>
            </div>: null
            }

        <div className="row">
            {
              productos.map((producto) => 
                    <>
                     <div className="col-xl-3 col-lg-3 col-md-4 col-sm-6 mb-3">
            <div className="card shadow h-100">
                <img src={producto.prod_img} alt="" height="200px"  className="img-card-top"/>
                <div className="card-body">
                    <p className="card-title">{producto.prod_nom}
                     </p>
                     <p className="card-text">
                         <small className="text-muted">Precio:</small> <br/>
                         <strong> S/ {producto.prod_pre}</strong>
                     </p>
                </div>
                <div className="card-footer text-right">
                    <button className="btn btn-outline-dark" >
                        <i className="fa fa-shopping-cart" aria-hidden="true"></i>
                    </button>
                </div>
            </div>
        </div>
                 
                    </>
            
              )}
              </div>
              </main>
              </>
    )
}

export default CarritoProductos
